<template>
  <div>
    <div class="row">
      <div class="col-md-12">
        <div class="card card-custom gutter-b">
          <div class="card-header">
            <div class="card-title">
              <h3 class="card-label">
                Sinyal <span v-if="threshold > 0" class="badge" style="background-color:#00ff00">{{ threshold }}</span>
              </h3>
            </div>
            <div class="card-toolbar">
              <button
                v-if="!(running_request > 0)"
                :disabled="sinyal.fetching"
                @click="registerAndStartFetchSinyal()"
                class="btn btn-primary mr-1"
                title="Klik reset untuk menerapkan perubahan modal ke semua coin"
              >
                <b-icon-back /> Reset
              </button>
              <span v-if="running_request > 0" class="text-muted"><b-spinner small /> {{ running_request }} API Call Running </span>
              <button v-else :disabled="sinyal.fetching" @click="startFetchSinyal()" class="btn btn-primary" title="Klik refresh untuk memperbarui data"><b-icon-clock /> Refresh</button>
            </div>
          </div>
          <div class="card-body">
            <b-row> </b-row>
            <b-row>
              <b-col md="3">
                <b-form-group id="modal_kiri" label="Modal Kiri:" label-cols-sm="6" label-align-sm="right" label-for="modal_kiri">
                  <b-form-input id="modal_kiri" type="number" placeholder="Modal Kiri" :value="modal_kiri" @input="updateModalKiri" required></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="3">
                <b-form-group id="modal_kanan" label="Modal Kanan:" label-cols-sm="6" label-align-sm="right" label-for="modal_kanan">
                  <b-form-input id="modal_kanan" type="number" placeholder="Modal Kanan" :value="modal_kanan" @input="updateModalKanan" required></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="3">
                <b-form-group id="limit_threshold" label="Limit Selisih:" label-cols-sm="6" label-align-sm="right" label-for="limit_threshold">
                  <b-form-input id="limit_threshold" type="number" placeholder="Limit Selisih" v-model="limit_threshold" required></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="3" class="text-center font-weight-bold">
                <span class="">Last Fetch:{{ lastFetchSinyal }}</span>
              </b-col>
            </b-row>
            <b-table bordered sticky-header="900px" head-variant="dark" responsive="" hover :items="listSinyal" :fields="sinyal.fields" :tbody-tr-class="rowClass">
              <template slot="thead-top" style="position:sticky;">
                <b-tr>
                  <b-th></b-th>
                  <b-th colspan="1">BINANCE</b-th>
                  <b-th colspan="1">1INCH</b-th>
                  <b-th colspan="2" variant="primary">SELISIH</b-th>
                  <b-th>SYMBOL</b-th>
                  <b-th></b-th>
                  <b-th colspan="1">BINANCE</b-th>
                  <b-th colspan="1">1INCH</b-th>
                  <b-th colspan="2" variant="primary">SELISIH</b-th>
                  <b-th></b-th>
                </b-tr>
              </template>
              <template #cell(symbol)="data">
                <a :href="listSettings.url_coin + data.item.coin_address" target="_pasar">{{ data.value }}</a>
                <br />
                <small class="text-muted">{{ data.item.pair }}</small>
              </template>
              <template #cell(binance_ask_price)="data">
                <span :id="'binance_ask_price_' + data.item.symbol" v-html="data.value"></span>
                <br />
                <button class="ml-2" title="Klik untuk copy" @click="CopyToClipboard('binance_ask_price_' + data.item.symbol)"><b-icon-clipboard /></button>
              </template>
              <template #cell(binance_bid_price)="data">
                <span :id="'binance_bid_price_' + data.item.symbol" v-html="data.value"></span>
                <br />
                <button class="ml-2" title="Klik untuk copy" @click="CopyToClipboard('binance_bid_price_' + data.item.symbol)"><b-icon-clipboard /></button>
              </template>
              <template #cell(inch_usdt)="data">
                <span :id="'inch_usdt' + data.item.symbol" v-html="data.value"></span>
                <br />
                <button class="ml-2" title="Klik untuk copy" @click="CopyToClipboard('inch_usdt' + data.item.symbol)"><b-icon-clipboard /></button>
              </template>
              <template #cell(selisih_pnl_kiri)="data">
                <span v-bind:class="{ 'bg-primary text-white p-2': parseFloat(data.value) >= parseFloat(limit_threshold) }" v-html="data.value"></span>
              </template>
              <template #cell(selisih_pnl_kiri_persen)="data" class="text-center">
                <span v-html="data.value"></span>
              </template>
              <template #cell(inch_price)="data">
                <span :id="'inch_price' + data.item.symbol" v-html="data.value"></span>
                <br />
                <button class="ml-2" title="Klik untuk copy" @click="CopyToClipboard('inch_price' + data.item.symbol)"><b-icon-clipboard /></button>
              </template>
              <!-- <template #cell(inch_sum)="data">
                <span v-html="data.value"></span>
              </template> -->
              <template #cell(selisih_pnl_kanan)="data">
                <span v-bind:class="{ 'bg-primary text-white p-2': parseFloat(data.value) >= parseFloat(limit_threshold) }" v-html="data.value"></span>
              </template>
              <template #cell(selisih_pnl_kanan_persen)="data">
                <span v-html="data.value"></span>
              </template>
              <template #cell(modal_kiri)="data">
                <input class="form-control" type="number" v-on:change="indvModal($event, data.item.symbol, 'kiri')" :value="data.value" title="Ketikkan modal, tekan enter untuk memuat." />
              </template>
              <template #cell(modal_kanan)="data">
                <input class="form-control" type="number" v-on:change="indvModal($event, data.item.symbol, 'kanan')" :value="data.value" title="Ketikkan modal, tekan enter untuk memuat." />
              </template>
              <template #cell(num)="data">
                <span v-html="data.index + 1"></span>
              </template>
            </b-table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";
import { mapGetters } from "vuex";
import axios from "axios";
import Swal from "sweetalert2";

export default {
  data() {
    return {
      sinyal: {
        fetching: false,
        totalRows: 1,
        currentPage: 1,
        perPage: 200,
        filter: null,
        filterOn: ["symbol"],
        // Note 'isActive' is left out and will not appear in the rendered table
        fields: [
          {
            key: "modal_kiri",
            label: "Modal",
            sortable: false,
            tdClass: "text-center font-weight-bold",
            thStyle: {
              width: "10%",
            },
          },
          {
            key: "binance_ask_price",
            label: "Price (Ask)",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-binance",
          },
          {
            key: "inch_usdt",
            label: "USDT",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-1inch",
          },
          {
            key: "selisih_pnl_kiri",
            label: "PNL",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "selisih_pnl_kiri_persen",
            label: "%",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "symbol",
            label: "Coin",
            sortable: true,
            tdClass: "text-center font-weight-bold bg-grey-100 text-dinamis",
            thStyle: {
              width: "40%",
            },
          },
          {
            key: "modal_kanan",
            label: "Modal",
            sortable: false,
            tdClass: "text-center font-weight-bold",
            thStyle: {
              width: "10%",
            },
          },
          {
            key: "binance_bid_price",
            label: "Price (Bid)",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-binance",
          },
          {
            key: "inch_price",
            label: "Price",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-price-1inch",
          },
          // {
          //   key: "inch_sum",
          //   label: "SUM",
          //   sortable: false,
          //   tdClass: "text-center font-weight-bold",
          // },
          {
            key: "selisih_pnl_kanan",
            label: "PNL",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "selisih_pnl_kanan_persen",
            label: "%",
            sortable: false,
            tdClass: "text-center font-weight-bold",
          },
          {
            key: "num",
            label: "#",
            sortable: false,
            tdClass: "text-center font-weight-bold bg-ireng text-white",
          },
        ],
        items: [],
      },
      modal_kiri: 0,
      modal_kanan: 0,
      running_request: 0,
      threshold: 0,
      limit_threshold: 3,
    };
  },
  components: {},
  watch: {
    limit_threshold() {
      this.threshold = 0;
      console.log(this.limit_threshold);
      this.listSinyal.forEach(item => {
        if (parseFloat(item.selisih_pnl_kanan) >= this.limit_threshold || parseFloat(item.selisih_pnl_kiri) >= this.limit_threshold) this.threshold++;
      });
    },
  },
  computed: {
    ...mapGetters(["listSinyal", "lastFetchSinyal", "listSettings"]),
  },
  mounted() {
    this.modal_kiri = this.listSettings.modal_kiri || 0;
    this.modal_kanan = this.listSettings.modal_kanan || 0;
    this.$store.dispatch(SET_BREADCRUMB, [{ title: "Market Watch" }]);
  },
  methods: {
    indvModal(val, sym, k) {
      console.log(val.target.value, sym, k);
      this.fetchSinyal(sym, { ["modal_" + k]: val.target.value });
    },
    updateModalKiri(e) {
      this.$store.dispatch("settingSet", { name: "modal_kiri", value: e });
    },
    updateModalKanan(e) {
      this.$store.dispatch("settingSet", { name: "modal_kanan", value: e });
    },
    rowClass(item, type) {
      if (!item || type !== "row") return;
      if (parseFloat(item.selisih_pnl_kanan) >= this.limit_threshold || parseFloat(item.selisih_pnl_kiri) >= this.limit_threshold) return "bg-green";
    },
    refreshSinyal() {
      this.$store.dispatch("registerSinyal");
    },
    test2() {
      // let symbol = "ALICE";
      // let coin = this.$store.state.coin.coins.find(item => item.nama_coin == symbol);
      let modal = 400;
      // let askPrice = 5.2024;
      // let api = BigInt("396470875063322314108");
      let api = BigInt("358516543755854128279");
      let pembagi = BigInt("1".padEnd(18 + 1, "0"));
      // let hasil = modal / askPrice;
      let hasil = (Number((api * 100n) / pembagi) / 100 - modal).toFixed(2);
      console.log(pembagi);
      console.log(hasil);
    },
    test3() {
      // amount=(10^desimal)*(modal/binance_ask_price)
      // 76887590342918700000
      let symbol = "1INCH";
      let coin = this.$store.state.coin.coins.find(item => item.nama_coin == symbol);
      // let pengali = BigInt("1".padEnd(parseInt(coin.desimal) + 1, "0"));
      let modal = 400;
      let askPrice = 5.2024;
      let hasil = (modal / askPrice) * Math.pow(10, parseInt(coin.desimal));
      console.log(hasil.toString());
    },
    test4() {
      let hasil = this.modal_kanan * Math.pow(10, 18);
      console.log(hasil);
    },
    registerAndStartFetchSinyal() {
      this.threshold = 0;
      this.$store.dispatch("clearSinyal").then(() => {
        this.running_request++;
        this.$store.dispatch("updateBookTicker").then(response => {
          console.log("respon", response);
          if (response.status != "ok") {
            Swal.fire({
              title: "Gagal memuat book ticker!",
              text: response.message,
              icon: "error",
              heightAuto: false,
            });
          } else {
            this.running_request -= 1;
            this.$store.dispatch("registerSinyal").then(() => {
              this.listSinyal.forEach(item => {
                this.fetchSinyal(item.symbol);
              }); // forEach
            }); // dispatch registerSinyal
            this.$bvToast.toast("Book Ticker berhasil binance di update", {
              title: `Binance Book Ticker`,
              variant: "success",
              solid: true,
            });
          }
        });
      }); // dispatch clearSinyal
    },
    startFetchSinyal() {
      this.threshold = 0;
      this.$store.dispatch("clearSinyal").then(() => {
        this.running_request++;
        this.$store.dispatch("updateBookTicker").then(response => {
          console.log("respon", response);
          if (response.status != "ok") {
            Swal.fire({
              title: "Gagal memuat book ticker!",
              text: response.message,
              icon: "error",
              heightAuto: false,
            });
          } else {
            this.running_request -= 1;
            this.listSinyal.forEach(item => {
              this.fetchSinyal(item.symbol);
            }); // forEach
            this.$bvToast.toast("Book Ticker berhasil binance di update", {
              title: `Binance Book Ticker`,
              variant: "success",
              solid: true,
            });
          }
        });
      }); // dispatch clearSinyal
    },
    fetchSinyal(symbol, addition) {
      this.running_request += 2;
      let sinyal = this.listSinyal.find(item => item.symbol == symbol);
      let coin = this.$store.state.coin.coins.find(item => item.nama_coin == symbol);
      if (addition == undefined) {
        addition = {};
      }
      let askPrice = this.$store.getters.getBookCoinPriceAsk(symbol);
      let bidPrice = this.$store.getters.getBookCoinPriceBid(symbol);
      this.$store
        .dispatch("updateSinyal", {
          symbol: symbol,
          value: {
            ...addition,
            binance_ask_price: askPrice.price,
            binance_bid_price: bidPrice.price,
            selisih_pnl_kiri: "<div class='spinner-border spinner-border-sm'></div>",
            inch_usdt: "<div class='spinner-border spinner-border-sm'></div>",
            selisih_pnl_kiri_persen: "<div class='spinner-border spinner-border-sm'></div>",
            inch_sum: "<div class='spinner-border spinner-border-sm'></div>",
            inch_price: "<div class='spinner-border spinner-border-sm'></div>",
            selisih_pnl_kanan: "<div class='spinner-border spinner-border-sm'></div>",
            selisih_pnl_kanan_persen: "<div class='spinner-border spinner-border-sm'></div>",
          },
        })
        .then(() => {
          if (sinyal.binance_ask_price == 0) {
            this.$store.dispatch("updateSinyal", {
              symbol: symbol,
              value: {
                ...addition,
                selisih_pnl_kiri: 0,
                inch_usdt: 0,
                selisih_pnl_kiri_persen: 0,
                inch_sum: 0,
                inch_price: 0,
                selisih_pnl_kanan: 0,
                selisih_pnl_kanan_persen: 0,
              },
            });
            this.running_request -= 2;
            return true;
          }
          let modal_kiri = addition.modal_kiri != undefined ? addition.modal_kiri : sinyal.modal_kiri;
          let modal_kanan = addition.modal_kanan != undefined ? addition.modal_kanan : sinyal.modal_kanan;
          // console.log(modal_kiri / askPrice);
          // console.log(((modal_kiri / askPrice) * Math.pow(10, parseInt(coin.desimal))).toLocaleString("fullwide", { useGrouping: false }));
          // console.log(parseInt((modal_kiri / askPrice) * Math.pow(10, parseInt(coin.desimal))));
          // let amount = BigInt(parseInt((modal_kiri / askPrice) * Math.pow(10, parseInt(coin.desimal)))).toString();
          // console.log(amount);
          let amount = ((modal_kiri / askPrice.price) * Math.pow(10, parseInt(coin.desimal))).toLocaleString("fullwide", { useGrouping: false, maximumFractionDigits: 0 });
          console.log(amount);
          axios
            .get("https://api.1inch.exchange/v3.0/56/quote", {
              params: {
                fromTokenAddress: coin.coin_address,
                toTokenAddress: this.$store.state.setting.settings.busd_address,
                amount,
              },
            })
            .then(response => {
              this.running_request -= 1;
              let data = response.data;

              // kalkulasi selisih pnl kiri
              let api = BigInt(data.toTokenAmount);
              let pembagi = BigInt("1".padEnd(19, "0"));
              let selisih_pnl_kiri = Number((api * 100n) / pembagi) / 100 - modal_kiri;

              // inch_usdt
              // ( modal_kiri + API_INCH_FROM ) / ( modal_kiri / binance_ask_price )
              let inch_usdt = (parseInt(modal_kiri) + selisih_pnl_kiri) / (parseInt(modal_kiri) / askPrice.price);

              // kalkulasi selisih_pnl_kiri_persen
              // ((inch_usdt-binance_ask_price)/binance_ask_price)*100
              let selisih_pnl_kiri_persen = ((inch_usdt - askPrice.price) / askPrice.price) * 100;

              this.$store.dispatch("updateSinyal", {
                symbol: symbol,
                value: {
                  selisih_pnl_kiri: selisih_pnl_kiri.toFixed(2),
                  inch_usdt: inch_usdt.toFixed(7),
                  selisih_pnl_kiri_persen: selisih_pnl_kiri_persen.toFixed(2),
                },
              });

              let amount2 = BigInt(modal_kanan * Math.pow(10, 18)).toLocaleString("fullwide", { useGrouping: false, maximumFractionDigits: 0 });
              axios
                .get("https://api.1inch.exchange/v3.0/56/quote", {
                  params: {
                    fromTokenAddress: this.$store.state.setting.settings.busd_address,
                    toTokenAddress: coin.coin_address,
                    amount: amount2,
                  },
                })
                .then(response => {
                  this.running_request -= 1;
                  let data2 = response.data;

                  // kalkulasi inch_sum
                  let api2 = BigInt(data2.toTokenAmount);
                  let pembagi = BigInt("1".padEnd(parseInt(coin.desimal) + 1, "0"));
                  let inch_sum = Number((api2 * 100n) / pembagi) / 100;

                  // kalkulasi inch_price
                  // modal_kanan/API_INCH_TO
                  let inch_price = inch_sum == 0 ? 0 : modal_kanan / inch_sum;

                  // kalkulasi selisih_pnl_kanan
                  // ((API_INCH_TO*binance_bid_price)-modal_kanan)
                  let selisih_pnl_kanan = inch_sum * bidPrice.price - modal_kanan;

                  // kalkulasi selisih_pnl_kanan_persen
                  // ((binance_bid_price-inch_price)/inch_price)*100
                  let selisih_pnl_kanan_persen = inch_sum == 0 ? 0 : ((bidPrice.price - inch_price) / inch_price) * 100;
                  if (selisih_pnl_kanan >= this.limit_threshold || selisih_pnl_kiri >= this.limit_threshold) this.threshold++;

                  this.$store.dispatch("updateSinyal", {
                    symbol: symbol,
                    value: {
                      inch_sum: inch_sum.toFixed(2),
                      inch_price: inch_price.toFixed(7),
                      selisih_pnl_kanan: selisih_pnl_kanan.toFixed(2),
                      selisih_pnl_kanan_persen: selisih_pnl_kanan_persen.toFixed(2),
                    },
                  });
                })
                .catch(error => {
                  this.running_request -= 1;
                  this.$bvToast.toast("Error saat menghubungi 1INCH untuk koin " + symbol, {
                    title: `API Error`,
                    variant: "danger",
                    solid: false,
                  });

                  this.$store.dispatch("updateSinyal", {
                    symbol: symbol,
                    value: {
                      inch_sum: 0,
                      inch_price: 0,
                      selisih_pnl_kanan: 0,
                      selisih_pnl_kanan_persen: 0,
                    },
                  });
                  console.log("Fetching 1inch error", error);
                });
            })
            .catch(error => {
              this.running_request -= 2;
              this.$bvToast.toast("Error saat menghubungi 1INCH untuk koin " + symbol, {
                title: `API Error`,
                variant: "danger",
                solid: false,
              });
              this.$store.dispatch("updateSinyal", {
                symbol: symbol,
                value: {
                  selisih_pnl_kiri: "error",
                  inch_usdt: "error",
                  selisih_pnl_kiri_persen: "error",
                  inch_sum: "error",
                  inch_price: "error",
                  selisih_pnl_kanan: "error",
                  selisih_pnl_kanan_persen: "error",
                },
              });
              console.log("Fetching 1inch error", error);
            });
        }); // dispatch updateSinyal
    },
    CopyToClipboard(containerid) {
      console.log(containerid);
      let selrange = document.createRange();
      selrange.selectNode(document.getElementById(containerid));
      window.getSelection().removeAllRanges(); // clear current selection
      window.getSelection().addRange(selrange); // to select text
      document.execCommand("copy");
      window.getSelection().removeAllRanges(); // to deselect
      this.$bvToast.toast("data dicopy", {
        title: `Clipboard`,
        variant: "info",
        solid: false,
      });
    },
  },
};
</script>
